  right: 12px;\r\n  top: 50%;\r\n  transform: translateY(-50%);\r\n  background: none;\r\n  border: none;\r\n  color: var(--text-light);\r\n  cursor: pointer;\r\n}\r\n\r\n.auth-form small {\r\n  display: block;\r\n  margin-top: 8px;\r\n  color: var(--text-light);\r\n  font-size: 12px;\r\n}\r\n\r\n.auth-button {\r\n  width: 100%;\r\n  padding: 12px;\r\n  background-color: var(--primary-color);\r\n  color: white;\r\n  border: none;\r\n  border-radius: var(--border-radius);\r\n  font-size: 16px;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  transition: background-color var(--transition-speed);\r\n}\r\n\r\n.auth-button:hover {\r\n  background-color: var(--primary-dark);\r\n}\r\n\r\n.auth-button i {\r\n  margin-right: 8px;\r\n}\r\n\r\n/* Form Footer */\r\n.form-footer {\r\n  margin-top: 24px;\r\n  text-align: center;\r\n  color: var(--text-light);\r\n  background: none;\r\n  border: none;\r\n  padding: 0;\r\n  box-shadow: none;\r\n}\r\n\r\n.form-footer p {\r\n  margin: 0;\r\n  padding: 0;\r\n  background: none;\r\n  border: none;\r\n  box-shadow: none;\r\n}\r\n\r\n.form-footer a {\r\n  color: var(--primary-color);\r\n  text-decoration: none;\r\n  font-weight: 500;\r\n  background: none;\r\n  border: none;\r\n  padding: 0;\r\n  box-shadow: none;\r\n  transition: color var(--transition-speed);\r\n}\r\n\r\n.form-footer a:hover {\r\n  color: var(--primary-dark);\r\n  text-decoration: underline;\r\n}\r\n\r\n/* Form */\r\n.form-container {\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n  background-color: var(--background-white);\r\n  padding: 32px;\r\n  border-radius: var(--border-radius);\r\n  box-shadow: var(--shadow-medium);\r\n}\r\n\r\n.form-group {\r\n  margin-bottom: 24px;\r\n}\r\n\r\n.form-group label {\r\n  display: block;\r\n  margin-bottom: 8px;\r\n  font-weight: 500;\r\n  color: var(--text-dark);\r\n}\r\n\r\n.form-group label i {\r\n  color: var(--primary-color);\r\n  margin-right: 8px;\r\n}\r\n\r\n.form-group input,\r\n.form-group textarea {\r\n  width: 100%;\